let bundlesManifest = null;
const activated = Object.create(null);

async function loadManifest() {
  if (bundlesManifest) return bundlesManifest;
  try {
    const res = await fetch("/section-bundles.json", { cache: "no-cache" });
    if (!res.ok) throw new Error(`HTTP ${res.status}`);
    bundlesManifest = await res.json();
    return bundlesManifest;
  } catch (e) {
    console.log(`Failed to load section-bundles.json: ${e.message}`);
    return {};
  }
}

export async function isSectionActivated(section) {
  return !!activated[section];
}

export async function activateSection(section) {
  if (activated[section]) return true;
  const manifest = await loadManifest();
  const url = manifest[section];
  if (!url) {
    console.log(`No bundle found for section "${section}".`);
    return false;
  }

  const startTime = performance.now();
  console.log(`Preload section "${section}" and its assets`);

  try {
    await import(/* @vite-ignore */ url);
    activated[section] = true;
    const duration = performance.now() - startTime;
    console.log(`Preload "${section}" Assets and section "${section}" cached`);
    console.log(`Preloading completed in ${duration.toFixed(2)}ms`);
    return true;
  } catch (e) {
    console.log(`Failed to preload section "${section}": ${e.message}`);
    return false;
  }
}
