import { defineStore } from "pinia";

export const useSectionsStore = defineStore("sections", {
  state: () => ({
    activated: {
      auth: false,
      dashboard: false,
      profile: false,
      discover: false,
      shop: false,
      misc: false,
    },
  }),
  actions: {
    markActivated(section) {
      this.activated[section] = true;
    },
    isActivated(section) {
      if (this.activated[section]) {
        console.log(`Section "${section}" already cached.`);
        return true;
      }
      return false;
    },
  },
});
